import { Injectable } from '@angular/core';
import { signal } from '@angular/core';
import { TodoTask, Category } from '../models';

@Injectable({
  providedIn: 'root'
})
export class CalendarService {
  
  // 카테고리 목록
  categories = signal<Category[]>([
    { name: '업무', color: '#4f46e5' },
    { name: '개인', color: '#10b981' },
    { name: '가족', color: '#f59e0b' },
    { name: '기타', color: '#6b7280' },
  ]);

  // 할 일 데이터
  tasks = signal<TodoTask[]>([
    {
      id: '1',
      dateStr: this.formatDate(new Date()),
      title: '주간 업무 보고',
      completed: false,
      color: '#4f46e5',
      time: '10:00',
      category: '업무',
      memo: '이번 주 주요 이슈 정리',
    },
    {
      id: '2',
      dateStr: this.formatDate(new Date()),
      title: '점심 약속',
      completed: false,
      color: '#10b981',
      time: '12:30',
      category: '개인',
      memo: '강남역 11번 출구',
    },
    {
      id: '3',
      dateStr: this.formatDate(new Date(new Date().setDate(new Date().getDate() + 2))),
      title: '디자인 리뷰',
      completed: false,
      color: '#4f46e5',
      time: '14:00',
      category: '업무',
    },
  ]);

  constructor() {}

  // 날짜를 YYYY-MM-DD 형식으로 변환
  formatDate(date: Date): string {
    const year = date.getFullYear();
    const month = String(date.getMonth() + 1).padStart(2, '0');
    const day = String(date.getDate()).padStart(2, '0');
    return `${year}-${month}-${day}`;
  }

  // 특정 날짜의 할 일 목록 조회
  getTasksByDate(dateStr: string): TodoTask[] {
    return this.tasks().filter(t => t.dateStr === dateStr);
  }

  // 특정 날짜의 할 일 목록 (시간순 정렬)
  getTasksByDateSorted(dateStr: string): TodoTask[] {
    return this.tasks()
      .filter(t => t.dateStr === dateStr)
      .sort((a, b) => {
        if (!a.time && !b.time) return 0;
        if (!a.time) return -1;
        if (!b.time) return 1;
        return a.time.localeCompare(b.time);
      });
  }

  // 새로운 할 일 추가 (기본)
  addTask(dateStr: string, title: string, color: string): void {
    if (!title.trim()) return;
    
    const newTask: TodoTask = {
      id: crypto.randomUUID(),
      dateStr,
      title: title.trim(),
      completed: false,
      color
    };
    
    this.tasks.update(prev => [...prev, newTask]);
  }

  // 새로운 할 일 추가 (상세)
  addTaskDetailed(task: Omit<TodoTask, 'id'>): void {
    const title = (task as any).title as string;
    if (!title.trim()) return;
    
    const newTask: TodoTask = {
      ...task,
      id: crypto.randomUUID(),
      title: title.trim(),
    };
    
    this.tasks.update(prev => [...prev, newTask]);
  }

  // 할 일 완료/미완료 토글
  toggleTask(id: string): void {
    this.tasks.update(prev => 
      prev.map(t => t.id === id ? { ...t, completed: !t.completed } : t)
    );
  }

  // 할 일 삭제
  deleteTask(id: string): void {
    this.tasks.update(prev => prev.filter(t => t.id !== id));
  }

  // 카테고리 추가
  addCategory(name: string, color: string): void {
    if (!name.trim()) return;
    const newCat: Category = { name: name.trim(), color };
    this.categories.update(cats => [...cats, newCat]);
  }

  // 검색
  searchTasks(query: string): TodoTask[] {
    const q = query.toLowerCase().trim();
    if (!q) return [];
    return this.tasks()
      .filter(t => t.title.toLowerCase().includes(q))
      .sort((a, b) => a.dateStr.localeCompare(b.dateStr));
  }
}
